import React, { Component } from 'react';
import img1 from '../../assets/img/beauty_1630324846957.JPEG';
import img2 from '../../assets/img/IMG_4163.JPG';
import img3 from '../../assets/img/IMG_5513.JPEG';
import img4 from '../../assets/img/IMG_5857.JPEG';
import { Button } from '@mui/material';
import swal from 'sweetalert';
import Slider from '../slider';
import VanillaTilt from 'vanilla-tilt';

export default class PhotoPage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            happy: "",
            imgs: [img1, img2, img3, img4],
            imgContent: ['', '', '', '']
        }
    }
    render() {
        const element = document.querySelectorAll(".contentH2");
        VanillaTilt.init(element, {
            max: 25,
            speed: 400,
            glare: true,
            "max-glare": 0.5,
        });
        return (
            <div className='photePage'>
                <div className='swiperslider'>
                    <Slider imgs={ this.state.imgs }/>
                </div>
            <Button variant="contained" 
            onClick={() => {
                this.state.happy === '' 
                ? 
                this.setState({happy: ''}) 
                : 
                this.setState({happy: ''});
                swal({
                    title: '',
                    text: '~',
                    icon: 'success',
                    button: '~',
                })
                }
                }>{ this.state.happy }</Button>
                <ul className='photoPage'>
                    <li>
                        <img className="img" src={ img1 } alt='img1'/>
                        <h2></h2>
                    </li>
                    <li>
                        <img className="img" src={ img2 } alt='img2'/>
                        <h2></h2>
                    </li>
                    <li>
                        <img className="img" src={ img3 } alt='img3'/>
                        <h2></h2>
                    </li>
                    <li>
                        <img className="img" src={ img4 } alt='img4'/>
                        <h2></h2>
                    </li>
                </ul>
                {
                    this.state.imgs.map((value, idx) => {
                        let imgContent = '';
                        switch (idx) {
                            case (0): imgContent = '~';break;
                            case (1): imgContent = '~';break;
                            case (2): imgContent = '~';break;
                            case (3): imgContent = '~';break;
                            default: imgContent = '~';break;
                        }
                        if (idx % 2 === 0){
                            return (
                                <div className='content-img' >
                                    <div className={ `contentH2 content-img${idx}` } style={{ 
                                        backgroundImage: 'linear-gradient( 90.1deg,  rgba(255,85,85,1) 0.1%, rgba(85,85,255,1) 100% )'
                                        }}>
                                        <h2>{ imgContent }</h2>
                                    </div>
                                    <img className='contentImg' id={ `contentImg${idx + 1}`} src={ value } alt={ `img${idx + 1}` } style={{ height: 'auto', width: '300px'}}/>
                                </div>
                            )
                        } 
                        return (
                        <div className='content-img' >
                            <img className='contentImg' id={`contentImg${idx + 1}`} src={ value } alt={ `img${idx + 1}` } style={{ height: 'auto', width: '300px'}}/>
                            <div className={`contentH2 content-img${idx}`} style={{ 
                                backgroundImage: 'radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% )'
                            }}>
                                  <h2>{ imgContent }</h2>
                            </div>
                        </div>
                        )
                    }
                    )
                }
            </div>
        )
    }
}
