import React, { Component, createRef } from 'react'
import Content from '../content';
import windyHill from '../../assets/audio/windyHill.mp3'
import toast, { Toaster } from 'react-hot-toast';


export default class Header extends Component {
    constructor (props) {
        super(props)
        this.state = {
            homeContent: '',
            photeContent: '',
            logInContent: '',
            welcomeContent: '~',
            toasterContent: ['!', '', '', ''],
            functionOnDevelop: '',
            isShowHome: true,
            selectColor: '#c02c38'
        }
        
        this.homePage = createRef()
        this.photoPage = createRef()
    }

    
    render() {
        const flower = <svg t="1640517270976" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1762" width="200" height="200"><path d="M935.4 374.9c-30.6-94.3-118.1-154.1-212.1-154.1-29-89.4-113-154.1-212.1-154.1S328 131.4 299 220.8c-94 0-181.5 59.9-212.1 154.1-30.6 94.3 4.9 194.1 81 249.4-29.1 89.4 0.8 191.1 81 249.4 80.2 58.3 186.1 55.3 262.2 0 76.1 55.3 182 58.2 262.2 0s110.1-159.9 81-249.4C930.4 569 966 469.2 935.4 374.9z" fill="#FF405D" p-id="1763"></path><path d="M935.4 374.9c-30.6-94.3-118.1-154.1-212.1-154.1-29-89.4-113-154.1-212.1-154.1-16.4 0-32.4 1.8-47.8 5.2 77.6 17 140.1 74.3 164.3 148.9 94 0 181.5 59.9 212.1 154.1 30.6 94.3-4.9 194.1-81 249.4 29.1 89.4-0.8 191.1-81 249.4-25.7 18.6-54 31-83.1 37.4 60 13.1 125.1 1.6 178.7-37.4 80.2-58.3 110.1-159.9 81-249.4 76-55.3 111.6-155.1 81-249.4z" fill="#CF354E" p-id="1764"></path><path d="M511.1 408.1c-12.6 0-22.8-10.2-22.8-22.8v-74.5c0-12.6 10.2-22.8 22.8-22.8 12.6 0 22.8 10.2 22.8 22.8v74.5c0 12.6-10.2 22.8-22.8 22.8zM389.9 496.2c-2.3 0-4.7-0.4-7.1-1.1l-70.8-23c-12-3.9-18.5-16.8-14.6-28.7 3.9-12 16.8-18.6 28.7-14.6l70.8 23c12 3.9 18.5 16.8 14.6 28.7-3 9.6-12 15.7-21.6 15.7zM392.4 698.9c-4.6 0-9.3-1.4-13.4-4.4-10.2-7.4-12.5-21.7-5-31.9l43.8-60.2c7.4-10.2 21.7-12.5 31.9-5 10.2 7.4 12.5 21.7 5 31.9l-43.8 60.2c-4.5 6.2-11.4 9.4-18.5 9.4zM629.8 698.9c-7.1 0-14-3.3-18.5-9.4l-43.8-60.2c-7.4-10.2-5.1-24.5 5-31.9 10.2-7.4 24.5-5.1 31.9 5l43.8 60.2c7.4 10.2 5.1 24.5-5 31.9-4 3-8.7 4.4-13.4 4.4zM632.3 496.2c-9.6 0-18.6-6.1-21.7-15.8-3.9-12 2.7-24.9 14.6-28.7l70.8-23c12-3.9 24.9 2.7 28.7 14.6 3.9 12-2.7 24.9-14.6 28.7l-70.8 23c-2.2 0.9-4.6 1.2-7 1.2z" fill="#CF354E" p-id="1765"></path><path d="M511.1 512.8m-127.5 0a127.5 127.5 0 1 0 255 0 127.5 127.5 0 1 0-255 0Z" fill="#FFD347" p-id="1766"></path><path d="M511.1 385.3c-16.9 0-33 3.3-47.8 9.3C510 413.5 543 459.3 543 512.8S510 612 463.3 631c14.8 6 30.9 9.3 47.8 9.3 70.4 0 127.5-57.1 127.5-127.5s-57.1-127.5-127.5-127.5z" fill="#FFBE31" p-id="1767"></path></svg>
        return (
            <>
            <div><Toaster/></div>
            <div className='Header'>
                <div className='logo' onClick={() => {
                    const randomNumber = Math.floor(Math.random() * this.state.toasterContent.length);
                    toast.error(this.state.toasterContent[randomNumber]);
                    }}>  
                    { flower }
                </div>
            <div 
            className='nav home' 
            onClick={ () => { 
                this.setState({isShowHome: true});
                this.photoPage.current.style.color = 'black';
                this.homePage.current.style.color = this.state.selectColor;
                toast.success('~', {icon: '', position: 'bottom-left'})
            }}
            ref={ this.homePage }
            >
               <h1>{ this.state.homeContent } </h1>
            </div>
            <h1>|</h1>    
            <div 
            className='nav photo' 
            onClick={ () => {
                this.setState({isShowHome: false});
                this.homePage.current.style.color = 'black';
                this.photoPage.current.style.color = this.state.selectColor;
                toast.success('~', {icon: '', position: 'bottom-left'})
                }
            }
            ref = { this.photoPage }
            >
                <h1>{ this.state.photeContent }</h1>
            </div>
                <audio className='audio1' src={ windyHill } loop autoPlay controls/>
            <div className='welcome'>
                <h1>{ this.state.welcomeContent }</h1>
            </div>
            <div className='nav logIn' onClick={() => 
            toast.error(
                this.state.functionOnDevelop, 
                {
                    position: 'bottom-right'
                }
                )}>
                <h1>{ this.state.logInContent }</h1>
            </div>
            </div>
            <div className='content'>
                <Content isShowHome={ this.state.isShowHome }/>
            </div>
            </>
        );
    }
}
